import React from 'react'
import './SignIn.css'
let fire = require('../../Fire.js')
let firebase = require('firebase')

class SignIn extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      shown: true
    }

    this.componentDidMount = this.componentDidMount.bind(this)
    this.stopShowing = this.stopShowing.bind(this)
  }

  componentDidMount() {
    let regainProgress = this.props.regainProgress
    let initUser = this.props.initUser
    let stopShowing = this.stopShowing
    var uiConfig = {
      callbacks: {
        signInSuccessWithAuthResult: function(authResult, redirectUrl) {
          let isNewUser = authResult.additionalUserInfo.isNewUser
          let uid = authResult.user.uid
          if (!isNewUser) {
            regainProgress(uid)
          } else {
            initUser(uid)
          }
          stopShowing()
          return false
        }
      },
      // Will use popup for IDP Providers sign-in flow instead of the default, redirect.
      signInFlow: 'popup',
      signInOptions: [firebase.auth.PhoneAuthProvider.PROVIDER_ID]
    }

    fire.ui.start('#firebaseui-auth-container', uiConfig)
  }

  stopShowing() {
    this.setState({shown: false})
  }

  render() {
    let className = ''
    if (!this.state.shown) {
      className += ' hide'
    }
    return (
      <div className={'surrounding' + className}>
        <div className={'signIn' + className}>
          <p className={'signInP' + className}>Sign in to regain progress or save your progress from the start</p>
          <div id="firebaseui-auth-container"></div>
        </div>
      </div>
    )
  }
}

export default SignIn
